import { useNavigate } from 'react-router-dom';
import './App.css';

import finalScreenMusic from './happybirthday.mp3'

import { useEffect, useMemo, useState, useRef, useContext } from "react";
import { appContext } from './App';
import { saveAs } from 'file-saver';



export const FinalScreen = () => {

    var FileSaver = require('file-saver');
    
    var base_image = new Image();
    base_image.src = 'data:image/png;base64,';

    base_image.onload = function(){
        
    }

    function saveCanvas(x_canvas: any){
        if (x_canvas.toBlob){
            x_canvas.toBlob( (blob1: string | Blob) => {
                saveAs(blob1, "birthday2024coupon.png");
            },"image/png");
        }
    }

    const [playing, setPlaying] = useState(false);
    const audio = useRef(new Audio(finalScreenMusic));

    const { totalWinnings, setTotalWinnings } = useContext(appContext);

    
    const play = () => {
        setPlaying(true);
        audio.current.play();
        audio.current.loop = true;
    };
    
    const pause = () => {
        setPlaying(false);
        audio.current.pause();
    };

    const saveCoupon = () => {
        //FileSaver.saveAs("./toronto.png", "image.png");
        var canvas = (document.getElementById('imageCanvas') as HTMLCanvasElement);
        var context = canvas.getContext('2d');     
        context?.drawImage(base_image, 0, 0);
        saveCanvas(canvas);
    }

    return (
        <div className="FinalBackground">
            <label className="dollars">Current Prize Dollars: ${totalWinnings}</label>
            <button className="music" onClick={playing ? pause : play}>Play Music</button>
            <canvas id="imageCanvas" width={960} height={489} hidden/>
            <button className="centerbutton" onClick={saveCoupon}>Save Coupon</button>
        </div>  
    );  
};
